.start {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.start a{
    margin-left: 15px;
    color:white;
    text-decoration: none;
    margin-right: 15px;
}
.links {
    position: relative;
    width: 70%;
    margin: 0;
    padding: 0; 
    display: inline;
    justify-content: space-right;
  }
  
 .links a{
    display: inline-block;
    text-align: left;
    color: white;
    text-decoration: none;
    margin-left: 25px;
    margin-right: 5px;
  }

  .links a ul{
    display: none;
    list-style: none;
    position: absolute;
    margin-top: 15px;
    color: black;
    background-color: lightgray;
    border-radius: 0px 0px 8px 8px;
  }
  
  .links a ul a{
    display: inline-block;
    position: absolute;
    color: black;
    background-color: lightgray;

  }
  .links a ul li{
    padding: 1px 1px;
    border-radius: 4px;
    
   }

   .links a ul li a{
    text-align: justify;
    color: red;

   }

  .links a:hover ul{
    display: block;
   }

  .currentPage {
    border-bottom: 2px solid white;
  }

  .blockSatz{
    text-align: justify;
    hyphens: auto;
    margin-left: 15px;
    margin-right: 20px;
  }
